import React from 'react'
import { Backbone } from '../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../layout'
import { BigScreen, SmallScreen } from '../../../helpers/mediaQueries'
import { getGlobalStyle } from '../../../helpers'
import style from './BackBoneShelves.module.css'

interface BackboneShelvesProps {
  horizontal?: boolean;
  shelvesForRow?: number;
  shelvesQuantityDesktop?: number;
  shelvesQuantityMobile?: number;
}

export const BackboneShelves = ({
  horizontal = false,
  shelvesForRow = 5,
  shelvesQuantityDesktop = 10,
  shelvesQuantityMobile = 6
}: BackboneShelvesProps): React.ReactElement<BackboneShelvesProps> => {

  const calculateCustomWidthForShelves = (): string => { return `${100 / shelvesForRow}` }
  const customWidth = calculateCustomWidthForShelves()
  return (
    <>
      <BigScreen>
        <Row isWrap>
          {Array.from(Array(shelvesQuantityDesktop).keys()).map(value => {
            return (
              <React.Fragment key={value}>
                <Column
                  customWidth={customWidth}
                  padding='8px'
                  position='relative'
                >
                  <Container
                    absoluteDefault='topRight'
                    justifyContent='end'
                    padding='20px'
                    position='absolute'
                  >
                    <Column maxWidth='40px'>
                      <Spacer.Horizontal size={24} />
                      <Row>
                        <Backbone borderRadius='50%' height={40} />
                      </Row>
                      <Spacer.Horizontal size={24} />
                      <Row>
                        <Backbone borderRadius='50%' height={40} />
                      </Row>
                    </Column>
                  </Container>
                  <Column
                    borderEdge='all'
                    borderRadius='12px'
                    padding='16px'
                  >
                    <Row justifyContent='center'>
                      <Row maxWidth='95%'>
                        <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={180} />
                      </Row>
                    </Row>
                    <Spacer.Horizontal size={12} />
                    <Row>
                      <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={52} />
                    </Row>
                    <Spacer.Horizontal size={12} />
                    <Row maxWidth='70%'>
                      <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={35} />
                    </Row>
                    <Spacer.Horizontal size={32} />
                    <Row justifyContent='center'>
                      <Row maxWidth='80%'>
                        <Backbone borderRadius='20px' height={30} />
                      </Row>
                    </Row>
                  </Column>
                </Column>
              </React.Fragment>
            )
          })}
        </Row>
      </BigScreen>

      <SmallScreen>
        <>
          <Column justifyContent='center'>
            {
              horizontal ?
                (
                  <Row
                    backgroundColor={getGlobalStyle('--color-alvi-background-wild-sand')}
                    isWrap
                    justifyContent='center'
                    padding='6px'
                  >
                    {
                      Array.from(Array(shelvesQuantityMobile).keys()).map(value => {
                        return (
                          <React.Fragment key={value}>
                            <Spacer.Horizontal size={8} />
                            <Column
                              backgroundColor={getGlobalStyle('--color-base-white')}
                              borderRadius={getGlobalStyle('--border-radius-sm')}
                              className={style.shadow}
                              maxWidth='95%'
                              padding='6px'
                            >
                              <Row justifyContent='around' padding='6px'>
                                <Column
                                  justifyContent='center'
                                  maxHeight='110px'
                                  maxWidth='110px'
                                  width='25%'
                                >
                                  <Backbone
                                    borderRadius={getGlobalStyle('--border-radius-sm')}
                                    height={110}
                                    width={100}
                                  />
                                </Column>

                                <Spacer.Vertical size={12} />

                                <Column
                                  justifyContent='center'
                                  maxWidth='50%'
                                >
                                  <Row>
                                    <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={50} />
                                  </Row>
                                  <Spacer.Horizontal size={12} />
                                  <Row>
                                    <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={40} />
                                  </Row>
                                </Column>

                                <Spacer.Vertical size={12} />

                                <Column maxWidth='35px'>
                                  <Row>
                                    <Backbone borderRadius='50%' height={35} />
                                  </Row>
                                  <Spacer.Horizontal size={16} />
                                  <Row>
                                    <Backbone borderRadius='50%' height={35} />
                                  </Row>
                                </Column>
                              </Row>

                              <Spacer.Horizontal size={24} />

                              <Row justifyContent='center'>
                                <Row maxWidth='50%'>
                                  <Backbone borderRadius='20px' height={35} />
                                </Row>
                              </Row>
                            </Column>
                            <Spacer.Horizontal size={8} />
                          </React.Fragment>
                        )
                      })
                    }
                  </Row>
                )
                :
                (
                  <Row
                    backgroundColor='#F5F5F5'
                    isWrap
                    justifyContent='center'
                    padding='6px'
                  >
                    {Array.from(Array(shelvesQuantityMobile).keys()).map(value => {
                      return (
                        <React.Fragment key={value}>
                          <Column
                            customWidth='50'
                            maxWidth='300px'
                            padding='6px'
                            position='relative'
                          >
                            <Container
                              absoluteDefault='topRight'
                              justifyContent='end'
                              padding='15px'
                              position='absolute'
                            >
                              <Column maxWidth='35px'>
                                <Row>
                                  <Backbone borderRadius='50%' height={35} />
                                </Row>
                                <Spacer.Horizontal size={16} />
                                <Row>
                                  <Backbone borderRadius='50%' height={35} />
                                </Row>
                              </Column>
                            </Container>
                            <Column
                              backgroundColor='#FFFFFF'
                              borderRadius='16px'
                              padding='16px'
                            >
                              <Spacer.Horizontal size={16} />
                              <Row justifyContent='center'>
                                <Row maxWidth='75%'>
                                  <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={120} />
                                </Row>
                              </Row>
                              <Spacer.Horizontal size={12} />
                              <Row>
                                <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={50} />
                              </Row>
                              <Spacer.Horizontal size={12} />
                              <Row maxWidth='70%'>
                                <Backbone borderRadius={getGlobalStyle('--border-radius-sm')} height={40} />
                              </Row>
                              <Spacer.Horizontal size={32} />
                              <Row justifyContent='center'>
                                <Row maxWidth='80%'>
                                  <Backbone borderRadius='20px' height={35} />
                                </Row>
                              </Row>

                            </Column>
                          </Column>
                        </React.Fragment>
                      )
                    })}
                  </Row >
                )
            }
          </Column>
        </>
      </SmallScreen>
    </>
  )
}
